.Download-Link {
  position: relative;
  display: inline-block;
  .icon-container {
    background-color: $primary-action-color;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    padding: 20px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    vertical-align: middle;
    * {
      color: white;
    }
  }
  .download-size {
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    background-color: green;
    text-align: center;
    position: absolute;
    width: 30px;
    height: 18px;
    color: white;
    border-radius: 8px;
    &.position-by-button {
      top: 7px;
      right: -8px;
    }
    &.position-by-anchor {
      top: -20px;
      right: -10px;
    }
  }
}

.download-list-modal-container {
  width: 70%;
  position: relative;
  .DownloadListTable {
    padding: 20px;
    max-height: 700px;
    overflow: auto;
    table {
      position: relative;
      th {
        top: 0px;
        position: sticky;
        z-index: 12;
      }
    }
  }
  .create-package-container {
    border-top: 1px solid #dcdcdc;
  }
}
